<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>预约管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div style="margin-bottom: 20px;margin-top: 20px">
      <el-input v-model="params.name" style="width: 150px" placeholder="请输入姓名"></el-input>
      <el-select v-model="params.ystate" placeholder="请预约状态" style="width: 150px;margin-left: 5px">
        <el-option label="未完成" value="未完成"></el-option>
        <el-option label="已完成" value="已完成"></el-option>
      </el-select>
      <el-date-picker
          v-model="ytime"
          value-format="yyyy-MM-dd HH:mm:ss"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
          STYLE="width: 380px;margin-left: 5px"
          :clearable="false"
          @change="getBE()">
      </el-date-picker>
      <el-select v-model="params.ytype" placeholder="请选择就诊类型" style="width:180px;margin-left: 5px">
        <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
        </el-option>
      </el-select>
      <el-button type="success" style="margin-left: 10px" @click="findYu()">查询</el-button>
      <el-button type="danger" style="margin-left: 10px" @click="clearSearch()">清空</el-button>
      <el-button type="primary" style="margin-left: 100px" @click="addYu()">添加</el-button>
    </div>
    <div>
      <el-table
          :data="tableData"
          stripe
          border
          size="mini"
          style="width: 80%;">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" class="demo-table-expand">
              <el-form-item label="姓名">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label="状态">
                <template slot-scope="scope">
                  <el-tag type="danger" v-if="props.row.ystate==='未完成'">{{ props.row.ystate }}</el-tag>
                  <el-tag type="success" v-else>{{ props.row.ystate }}</el-tag>
                </template>
              </el-form-item>
              <el-form-item label="时间">
                <span>{{ props.row.ytime }}</span>
              </el-form-item>
              <el-form-item label="就诊类型">
                <span>{{ props.row.ytype }}</span>
              </el-form-item>
              <el-form-item label="备注">
                <span>{{ props.row.yremark }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="name"
            label="姓名">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="ystate"
            label="状态">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.ystate==='未完成'">{{ scope.row.ystate }}</el-tag>
            <el-tag type="success" v-else>{{ scope.row.ystate }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="ytime"
            label="时间">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="ytype"
            label="就诊类型">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="yremark"
            label="备注">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            fixed="right"
            width=200
            label="操作">
          <template slot-scope="scope">
            <el-button type="primary" @click="editYu(scope.row)">编辑</el-button>
            <el-popconfirm title="确定删除吗？" @confirm="delYu(scope.row.id)">
              <el-button slot="reference" type="danger" style="margin-left: 10px">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[5, 10, 50, 100]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="total"
          style="margin-top: 20px"
      >
      </el-pagination>
    </div>
    <div>
      <el-dialog title="请填写信息" :visible.sync="dialogFormVisible" width="30%" :before-close="CancelDialog"
                 @open="more++">
        <el-form :key="more" :model="form" :rules="rules" ref="form" label-width="100px">

          <el-form-item label="姓名" label-width="15%" prop="name">
            <el-button type="primary" icon="el-icon-search" v-if="brname" @click="chooseBr()">搜索患者</el-button>
            <el-input v-model="form.name" autocomplete="off" style="width: 70%" disabled></el-input>
          </el-form-item>
          <el-form-item label="状态" label-width="15%" prop="ystate">
            <el-radio-group v-model="form.ystate">
              <el-radio label="未完成">
                <el-tag type="danger">未完成</el-tag>
              </el-radio>
              <el-radio label="已完成">
                <el-tag type="success">已完成</el-tag>
              </el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="时间" label-width="15%" prop="ytime">
            <el-date-picker
                v-model="form.ytime"
                type="datetime"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="类型" label-width="15%" prop="ytype">
            <el-select v-model="form.ytype" placeholder="请选择">
              <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="备注" label-width="15%" prop="yremark">
            <el-input
                type="textarea"
                :autosize="{ minRows: 2}"
                placeholder="请输入内容"
                v-model="form.yremark">
            </el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="CancelDialog">取 消</el-button>
          <el-button type="success" @click="resetRef">重置</el-button>
          <el-button type="primary" @click="submitYu()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <div>
      <el-dialog title="选择患者|单击表格单行数据选中" :visible.sync="dialogTableVisible"
                 :before-close="CancelTableDialog">
        <div>
          <el-input v-model="brparams.name" style="width: 200px" placeholder="请输入姓名"></el-input>
          <el-input v-model="brparams.tel" style="width: 200px; margin-left: 5px" placeholder="请输入电话"></el-input>
          <el-button type="success" style="margin-left: 10px" @click="findBr()">查询</el-button>
          <el-button type="danger" style="margin-left: 10px" @click="brclearSearch()">清空</el-button>
          <div style="margin-top: 20px" v-if="boolbr">
            已选中
            <el-tag type="success">{{ currentBr.name }}</el-tag>
            <el-button @click="setCurrentBr()" style="margin-left: 10px">取消选择</el-button>
          </div>
        </div>
        <el-table :data="gridData" border highlight-current-row style="margin-top: 20px" ref="brTable"
                  @current-change="brChange">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" class="demo-table-expand">
                <el-form-item label="姓名">
                  <span>{{ props.row.name }}</span>
                </el-form-item>
                <el-form-item label="性别">
                  <span>{{ props.row.sex }}</span>
                </el-form-item>
                <el-form-item label="年龄">
                  <span>{{ props.row.age }}</span>
                </el-form-item>
                <el-form-item label="电话">
                  <span>{{ props.row.tel }}</span>
                </el-form-item>
                <el-form-item label="来源">
                  <span>{{ props.row.bfrom }}</span>
                </el-form-item>
                <el-form-item label="地址">
                  <span>{{ props.row.address }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
              align="center"
              label-align="center"
              :show-overflow-tooltip="true"
              prop="name"
              width=100
              label="姓名">
          </el-table-column>
          <el-table-column
              align="center"
              label-align="center"
              :show-overflow-tooltip="true"
              prop="sex"
              width=100
              label="性别">
          </el-table-column>
          <el-table-column
              align="center"
              label-align="center"
              :show-overflow-tooltip="true"
              prop="age"
              width=100
              label="年龄">
          </el-table-column>
          <el-table-column
              align="center"
              label-align="center"
              :show-overflow-tooltip="true"
              prop="tel"
              width=120
              label="电话">
          </el-table-column>
          <el-table-column
              align="center"
              label-align="center"
              :show-overflow-tooltip="true"
              prop="bfrom"
              width=100
              label="来源">
          </el-table-column>
          <el-table-column
              align="center"
              label-align="center"
              :show-overflow-tooltip="true"
              prop="address"
              label="地址">
          </el-table-column>
        </el-table>
        <div>
          <el-pagination
              @size-change="brhandleSizeChange"
              @current-change="brhandleCurrentChange"
              :current-page="brparams.pageNum"
              :page-sizes="[5, 10, 50, 100]"
              :page-size="brparams.pageSize"
              layout="total, sizes, prev, pager, next"
              :total="brtotal"
              style="margin-top: 20px"
          >
          </el-pagination>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="CancelTableDialog">取 消</el-button>
          <el-button type="primary" @click="submitBr()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "YuView",
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      ytime: '',
      params: {
        name: '',
        stime: '',
        etime: '',
        ystate: '',
        pageNum: 1,
        pageSize: 5
      },
      total: 0,
      tableData: [],
      dialogFormVisible: false,
      form: {},
      rules: {
        name: [
          {required: true, message: '请选择患者', trigger: blur}
        ],
        ystate: [
          {required: true, message: '请选择状态', trigger: blur}
        ],
        ytime: [
          {required: true, message: '请选择时间', trigger: blur}
        ],
        ytype: [
          {required: true, message: '请选择就诊类型', trigger: blur}
        ],
        yremark: []
      },
      more: 0,
      brname: false,
      options: [{
        value: '洗牙',
        label: '洗牙'
      }, {
        value: '拔牙',
        label: '拔牙'
      }, {
        value: '补牙',
        label: '补牙'
      }, {
        value: '根管治疗',
        label: '根管治疗'
      }, {
        value: '固定义齿修复',
        label: '固定义齿修复'
      }, {
        value: '活动义齿修复',
        label: '活动义齿修复'
      }, {
        value: '种植牙',
        label: '种植牙'
      }, {
        value: '牙齿矫正',
        label: '牙齿矫正'
      }],
      dialogTableVisible: false,
      gridData: [],
      brparams: {
        name: '',
        tel: '',
        pageNum: 1,
        pageSize: 5
      },
      brtotal: 0,
      currentBr: {"name": ""},
      boolbr: false
    }
  },
  created() {
    this.findYu()
  },
  methods: {
    getBE() {
      if (this.ytime !== '') {
        this.params.stime = this.ytime[0].toString();
        this.params.etime = this.ytime[1].toString();
      }
    },

    findYu() {
      request.get("/findYu", {
        params: this.params
      }).then(res => {
        if (res.state === 0) {
          this.tableData = res.data.list;
          this.total = res.data.total;
          this.$message({
            message: '查询成功',
            type: 'success'
          });
        } else {
          this.$message.error(res.message);
          this.$router.push("/login")
        }
      })
    },

    handleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.findYu()
    },

    handleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.findYu()
    },

    clearSearch() {
      this.ytime = ''
      this.params = {
        name: '',
        stime: '',
        etime: '',
        ystate: '',
        pageNum: 1,
        pageSize: 5
      }
      this.findYu()
    },

    addYu() {
      this.form = {};
      this.brname = true
      this.dialogFormVisible = true;
    },
    editYu(obj) {
      this.form = obj;
      this.brname = false
      this.dialogFormVisible = true;
    },
    submitYu() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          request.post("/saveYu", this.form).then(res => {
            if (res.state === 0) {
              this.$message({
                message: '操作成功',
                type: 'success'
              });
              this.dialogFormVisible = false
              this.findYu()
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    },

    delYu(id) {
      request.delete("/delYu/" + id).then(res => {
        if (res.state === 0) {
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.findYu()
        } else {
          this.$message.error(res.message);
        }
      })
    },

    resetRef() {
      this.$refs.form.resetFields();
    },

    CancelDialog() {
      this.dialogFormVisible = false;
      this.resetRef();
    },

    chooseBr() {
      this.dialogFormVisible = false;
      this.dialogTableVisible = true;
      this.currentBr = {"name": ""}
      this.findBr()
    },

    findBr() {
      request.get("/findBr", {
        params: this.brparams
      }).then(res => {
        if (res.state === 0) {
          this.gridData = res.data.list;
          this.brtotal = res.data.total;
          this.$message({
            message: '查询成功',
            type: 'success'
          });
          if (this.gridData.length === 0) {
            this.$alert('未搜索到，请添加此患者', '提示', {
              confirmButtonText: '确定',
              callback: action => {
                this.dialogFormVisible = false
                this.dialogTableVisible = false
                this.$router.push({path: "/Br"})
              }
            });
          }
        } else {
          this.$message.error(res.message);
          this.$router.push("/login")
        }
      })
      if (this.currentBr.name !== "")
        this.setCurrentBr();
    },

    brhandleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.findBr()
    },

    brhandleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.findBr()
    },

    brclearSearch() {
      this.params = {
        name: '',
        tel: '',
        pageNum: 1,
        pageSize: 5
      }
      this.findBr()
    },

    brChange(val) {
      this.currentBr = val
      this.boolbr = true;
    },

    setCurrentBr(row) {
      this.$refs.brTable.setCurrentRow(row);
      this.boolbr = false;
      this.currentBr = {"name": ""};
    },

    CancelTableDialog() {
      this.brname = true
      this.dialogFormVisible = true;
      this.dialogTableVisible = false;
      this.setCurrentBr();
      this.$message.error('未选择患者');
    },

    submitBr() {
      if (this.currentBr.name === "")
        this.CancelTableDialog()
      else {
        this.form = {};
        this.form.brid = this.currentBr.id
        this.form.name = this.currentBr.name
        this.$message({
          type: 'success',
          message: '已选择患者' + this.currentBr.name
        });
        this.brname = true
        this.dialogFormVisible = true;
        this.dialogTableVisible = false;
        this.setCurrentBr();
      }
    },

  },

  watch: {
    ytime: {
      handler(val) {
        if (!val) {
          this.ytime = ''
          this.params.stime = ''
          this.params.etime = ''
        }
      }
    }
  }
}
</script>

<style scoped>

</style>